section {
    max-width: 600px;
    margin: 0 auto;
}

h1 {
    color: #84E;
    font-family: monospace;
}

p {
    font-size: 1.2em;
    line-height: 1.4;
    max-width: 400px;
}

a {
    display: block;
    max-width: 120px;
    padding: 10px;
    border-radius: 4px;
    text-align: center;
    background: #84E;
    color: white;
    text-decoration: none;
    text-transform: uppercase;
    font-family: monospace;
}